.main-nav__list {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-right: 35px;
}

.main-nav__list-item {
  padding: 10px;
  /* height: 100%;
  width: 100%; */
  margin: 0;
}
li.main-nav__list-item.add {
  background-color: rgb(199, 86, 20);
  color: black;
  border-radius: 5px;
  margin-right: 25px;
  cursor: pointer;
}

.main-nav__list-item:active,
.main-nav__list-item:hover,
li.main-nav__list-item.add:hover {
  background-color: rgb(116, 101, 101);
  color: white;
}
a {
  text-decoration: none;
  color: aliceblue;
  width: 100%;
}
