.landing__front {
  background-image: url('./landing.jpg');
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.landing_container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
}
.introduction {
  position: absolute;
  top: 350px;
  z-index: 100;
  background-color: rgb(236, 236, 236);
  border-radius: 15px;
  padding: 25px;
}
.introduction .title {
  font-size: large;
}
.introduction .signup_btn {
  border-radius: 10px;
  background-color: rgb(243, 160, 6);
  border: none;
  padding: 0.7rem;
}
